<script>
	import axios from 'axios';
	import laydate from 'layui-laydate';
	export default {
		data() {
			return {
				year: '',
				month: '',
				timeTableList: [],
				totalList: [],
				maxSite: {},
				minSite: {},
				maxTime: '',
				maxNumber: '',
				maxDbikeNumber: 0,
				maxSbikeNumber: 0,
				minTime: '',
				minNumber: '',
				minDbikeNumber: 0,
				minSbikeNumber: 0,
				bikeTotal: 0,
				dbikeTotalNum: 0,
				maxBike: {},
				bikeIncomePercent: '',
				bikeIncomePercentNum: 0,
				maxBattery: {},
				batteryIncomePercentNum: 0,
				batteryIncomePercent: '',
				bikeNumPercentNum: 0,
				bikeNumPercent: '',
				sbikeTotalNum: 0,
				bikeTotalIncome: 0,
				batteryTotalIncome: 0,
				dbikeTotal: 0,
				sbikeTotal: 0,
				hbatteryTotal: 0,
				dbatteryTotal: 0,
				rightMonthTableList: [],
				leftMonthTableList: [],
				zxcyztj: {},
				zxcfsdzl: {},
				zxcsr: {},
				zxcsrzb: {},
				zxcdd: {},
				zxcddzb: {},
				dpcsr: {},
				dpcsrzb: {},
				srzxcsczb: {},
				dzxcsczb: {}
			}
		},
		methods: {

		},
		mounted: function() {
			let that = this;
			let searchDate = window.location.href.split('?date=')[1];
			searchDate = searchDate.slice(0, 4) + '-' + searchDate.slice(4);
			that.year = searchDate.split('-')[0];
			that.month = searchDate.split('-')[1];
			axios.get('http://lease.smart-ideas.com.cn/nhpark/report/getLeaseSummary?date=' + searchDate)
				.then(function(res) {
					let bikeList = res.data;
					// 车船收入汇总
					let monthTableList = [];
					let dbikeTotal = 0;
					let sbikeTotal = 0;
					let hbatteryTotal = 0;
					let dbatteryTotal = 0;
					let bikeTotal = 0;
					let sbikeTotalNum = 0;
					let dbikeTotalNum = 0;
					let maxBike = {
						bikeIncome: 0,
						bikeTotal: 0,
						date: ''
					};
					let maxBattery = {
						batteryIncome: 0,
						hbatteryIncome: 0,
						dbatteryIncome: 0,
						date: ''
					};
					let dbikeIncomeList = [];
					let sbikeIncomeList = [];
					let dbikeNumList = [];
					let sbikeNumList = [];
					let hbatteryIncomeList = [];
					let dbatteryIncomeList = [];
					let timeList = [];
					bikeList.forEach(function(value, key) {
						let monthTableItem = {};
						monthTableItem.date = value.date;
						monthTableItem.dbikeIncome = value.dbikeIncome;
						monthTableItem.sbikeIncome = value.sbikeIncome;
						monthTableItem.dbikeNumber = value.dbikeTotal;
						monthTableItem.sbikeNumber = value.sbikeTotal;
						monthTableList.push(monthTableItem);
						dbikeTotal += value.dbikeIncome * 1;
						sbikeTotal += value.sbikeIncome * 1;
						dbikeTotalNum += value.dbikeTotal * 1;
						sbikeTotalNum += value.sbikeTotal * 1;
						dbikeIncomeList.push(value.dbikeIncome * 1);
						sbikeIncomeList.push(value.sbikeIncome * 1);
						dbikeNumList.push(value.dbikeTotal * 1);
						sbikeNumList.push(value.sbikeTotal * 1);
						if (value.dbikeIncome * 1 + value.sbikeIncome * 1 > maxBike.bikeIncome) {
							maxBike.bikeIncome = value.dbikeIncome * 1 + value.sbikeIncome * 1;
							maxBike.bikeTotal = value.dbikeTotal * 1 + value.sbikeTotal * 1;
							maxBike.date = value.date;
						}
						timeList.push(value.date.split('-')[1] + '-' + value.date.split('-')[2]);
					})
					axios.get('http://lease.smart-ideas.com.cn/nhpark/report/getBatterySummary?date=' + searchDate)
						.then(function(res) {
							let batteryList = res.data;
							batteryList.forEach(function(value, key) {
								monthTableList[key].hbatteryIncome = value.hbatteryIncome;
								monthTableList[key].dbatteryIncome = value.dbatteryIncome;
								hbatteryTotal += value.hbatteryIncome * 1;
								dbatteryTotal += value.dbatteryIncome * 1;
								hbatteryIncomeList.push(value.hbatteryIncome * 1);
								dbatteryIncomeList.push(value.dbatteryIncome * 1);
								if (value.hbatteryIncome * 1 + value.dbatteryIncome * 1 > maxBattery.batteryIncome) {
									maxBattery.batteryIncome = value.hbatteryIncome * 1 + value.dbatteryIncome * 1;
									maxBattery.date = value.date;
									maxBattery.hbatteryIncome = value.hbatteryIncome;
									maxBattery.dbatteryIncome = value.dbatteryIncome;
								}
							})
							let leftMonthTableList = [];
							let rightMonthTableList = [];
							monthTableList.forEach(function(value, key) {
								if (key < 16) {
									leftMonthTableList.push(value);
								} else {
									rightMonthTableList.push(value);
								}
							})
							let rightLastLength = 16 - rightMonthTableList.length;
							for (let i = 0; i < rightLastLength; i++) {
								let monthTableItem = {
									date: '',
									dbikeIncome: '',
									sbikeIncome: '',
									hbatteryIncome: '',
									dbatteryIncome: ''
								};
								rightMonthTableList.push(monthTableItem);
							}
							let bikeTotalIncome = dbikeTotal + sbikeTotal;
							let batteryTotalIncome = hbatteryTotal + dbatteryTotal;
							let bikeNumPercent = sbikeTotalNum > dbikeTotalNum ? '四人自行车' : '双人自行车';
							let bikeIncomePercent = sbikeTotal > dbikeTotal ? '四人自行车' : '双人自行车';
							bikeTotal = dbikeTotalNum + sbikeTotalNum;
							let batteryIncomePercent = hbatteryTotal > dbatteryTotal ? '环线' : '单站票';

							that.maxBattery = maxBattery;
							that.batteryIncomePercent = batteryIncomePercent;
							that.batteryIncomePercentNum = hbatteryTotal > dbatteryTotal ? parseInt(hbatteryTotal / batteryTotalIncome *
								100) : parseInt(dbatteryTotal / batteryTotalIncome * 100);
							that.bikeNumPercent = bikeNumPercent;
							that.bikeNumPercentNum = sbikeTotalNum > dbikeTotalNum ? parseInt(sbikeTotalNum / bikeTotal * 100) : parseInt(
								dbikeTotalNum / bikeTotal * 100);
							that.bikeIncomePercentNum = sbikeTotal > dbikeTotal ? parseInt(sbikeTotal / bikeTotalIncome * 100) : parseInt(
								dbikeTotal / bikeTotalIncome * 100);
							that.bikeIncomePercent = bikeIncomePercent;
